import { computed, reactive, ref, toRefs } from 'vue';
import { useTranslate } from '@demo/use-translate';
const i18n = {
    'zh-CN': {
        disableMenu: '禁用菜单',
        switchTitle1: '包邮',
        switchTitle2: '团购',
        itemTitle: '筛选',
        expandDirection: '向上展开',
        customContent: '自定义菜单内容',
        customActiveColor: '自定义选中态颜色',
        option1: [
            { text: '全部商品', value: 0 },
            { text: '新款商品', value: 1 },
            { text: '活动商品', value: 2 },
        ],
        option2: [
            { text: '默认排序', value: 'a' },
            { text: '好评排序', value: 'b' },
            { text: '销量排序', value: 'c' },
        ],
    },
    'en-US': {
        disableMenu: 'Disable Menu',
        switchTitle1: 'Title',
        switchTitle2: 'Title',
        itemTitle: 'Title',
        expandDirection: 'Expand Direction',
        customContent: 'Custom Content',
        customActiveColor: 'Custom Active Color',
        option1: [
            { text: 'Option1', value: 0 },
            { text: 'Option2', value: 1 },
            { text: 'Option3', value: 2 },
        ],
        option2: [
            { text: 'Option A', value: 'a' },
            { text: 'Option B', value: 'b' },
            { text: 'Option C', value: 'c' },
        ],
    },
};
export default {
    setup() {
        const item = ref();
        const t = useTranslate(i18n);
        const state = reactive({
            switch1: true,
            switch2: false,
            value1: 0,
            value2: 'a',
        });
        const option1 = computed(() => t('option1'));
        const option2 = computed(() => t('option2'));
        const onConfirm = () => {
            item.value?.toggle();
        };
        return {
            ...toRefs(state),
            t,
            item,
            option1,
            option2,
            onConfirm,
        };
    },
};
